@import url('https://fonts.googleapis.com/css?family=Patua+One');

html {
    font-size: 16px;
}

body {
    background-color: #282c34;
    color: #707b88;
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    font-family: 'Patua one', serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

h1,h2 {
    font-weight: normal;
    margin: 0.5rem 0;
}

h1 {
    font-size: 1.2rem;
}

h2 {
    font-size: 1.1rem;
}

ol {
    margin: 0;
    padding-left: 2rem;
}

li {
    cursor: pointer;
}

li:nth-child(2n) {
    color: #94a1b1;
}

li.done {
    color: #47515d;
    text-decoration: line-through;
}

a {
    color: #5485bf;
    text-decoration: none;
}

#root {
    width: 100vw;
}
